import * as React from "react";
import { BrowserRouter } from "react-router-dom";
import { QueryClient, QueryClientProvider } from "react-query";
import theme from "./util/theme"
import { Backdrop, CircularProgress } from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';
import { SnackbarProvider } from 'notistack';
import { IconButton } from "@mui/material";
import CloseIcon from '@mui/icons-material/Close';
import { Navigation } from "./component/Navigation";
import { AuthProvider } from "./context/Auth/authContext";
import { Layout } from "./component/Layout";
import { MediaQueriesProvider } from "./context/mediaQueriesContext";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import AdapterMoment from "@mui/lab/AdapterMoment";
import Keycloak from "keycloak-js";
import { ReactKeycloakProvider } from "@react-keycloak/web";
import isDocker from "./util/isDocker";
import auth from "./context/Auth/auth";

const keycloak = new Keycloak({
  url: `${isDocker() ? process.env.REACT_APP_KEYCLOAK_DOCKER_URL : process.env.REACT_APP_KEYCLOAK_URL}/auth`,
  realm: process.env.REACT_APP_KEYCLOAK_REALM,
  clientId: process.env.REACT_APP_KEYCLOAK_CLIENTID
});

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      // suspense: true,
    },
  },
});

const notistackRef = React.createRef();
const onClickDismiss = key => () => {
  notistackRef.current.closeSnackbar(key);
}

const App = () => {
  return (
    <BrowserRouter>
      <QueryClientProvider client={queryClient}>
        <ThemeProvider theme={theme}>
          <LocalizationProvider dateAdapter={AdapterMoment}>
            <MediaQueriesProvider>
              <SnackbarProvider
                ref={notistackRef}
                action={(key) => (
                  <IconButton onClick={onClickDismiss(key)}>
                    <CloseIcon />
                  </IconButton>
                )}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'center',
                }}
                maxSnack={3}>
                <Layout>
                  <React.Suspense fallback={<Backdrop
                    open={true}
                    sx={{ width: '100%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: 'rgba(0,0,0,0.1)', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                  >
                    <CircularProgress color="primary" />
                  </Backdrop>}>
                    <ReactKeycloakProvider authClient={keycloak} initOptions={{ onLoad: 'check-sso', token: auth.getToken(), refreshToken: auth.getRefreshToken() }}>
                      <AuthProvider>
                        <Navigation />
                      </AuthProvider>
                    </ReactKeycloakProvider>
                  </React.Suspense>
                </Layout>
              </SnackbarProvider>
            </MediaQueriesProvider>
          </LocalizationProvider>
        </ThemeProvider>
      </QueryClientProvider>
    </BrowserRouter>
  );
}

export default App;
