import * as React from "react";
import { get } from "lodash-es";
import { CssBaseline, Link, TableBody, TableCell, TableHead, TableRow } from '@mui/material';
import MaUTable from '@mui/material/Table'
import { useTable } from 'react-table'
import withEventService from "../../service/Event";
import useData from "../../hook/useData";
import EventItem from "../../component/Dashboard/EventItem/EventItem";
import styles from "./DashboardPage.module.scss";
import useAuth from "../../hook/useAuth";
import { Toolbar } from "../../component/Toolbar";
import useDialog from "../../hook/useDialog";
import { CreateEventDialog, UpdateEventDialog, DeleteEventDialog } from "./dialog";
import AvTimerIcon from '@mui/icons-material/AvTimer';
import LockIcon from '@mui/icons-material/Lock';
import DateRangeIcon from '@mui/icons-material/DateRange';
import PublicIcon from '@mui/icons-material/Public';
import GroupsIcon from '@mui/icons-material/Groups';
import WifiIcon from '@mui/icons-material/Wifi';
import moment from "moment";
import { TableActions } from "../../component/TableActions";
import { AuthContext } from "../../context/Auth/authContext";

const LazyUpdateEventDialog = React.lazy(() => import("./dialog/UpdateEventDialog"))

function Table({ columns, data }) {
    // Use the state and functions returned from useTable to build your UI
    const { getTableProps, headerGroups, rows, prepareRow } = useTable({
        columns,
        data,
    })

    // Render the UI for your table
    return (
        <MaUTable size="small" {...getTableProps()}>
            <TableHead>
                {headerGroups.map(headerGroup => (
                    <TableRow {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map(column => (
                            <TableCell key={`${column.id}`} {...column.getHeaderProps()}>
                                {column.render('Header')}
                            </TableCell>
                        ))}
                    </TableRow>
                ))}
            </TableHead>
            <TableBody>
                {rows.map((row, i) => {
                    prepareRow(row)
                    return (
                        <TableRow {...row.getRowProps()}>
                            {row.cells.map(cell => {
                                return (
                                    <TableCell {...cell.getCellProps()}>
                                        {cell.render('Cell')}
                                    </TableCell>
                                )
                            })}
                        </TableRow>
                    )
                })}
            </TableBody>
        </MaUTable>
    )
}

const DashboardPage = ({ readEvents, createEvent, updateEvent, deleteEvent }) => {
    const pre = "dashboard-page-";

    const { logged } = React.useContext(AuthContext);

    const onCreateEvent = async (values) => {
        await createEvent({
            data: values,
            createSuccessCb: () => {
                createDialog.handleClose()
            }
        });
    };

    const onUpdateEvent = async (id, values) => {
        await updateEvent({
            data: values,
            createSuccessCb: () => {
                updateDialog.handleClose();
            }
        })
    }

    const onDeleteEvent = async (id) => {
        await deleteEvent({ id })
    };

    const documents = useData();
    const createDialog = useDialog();
    const updateDialog = useDialog();
    const deleteDialog = useDialog();

    const columns = React.useMemo(() => [
        {
            Header: () => null,
            id: 'logo',
            Cell: ({ row }) => <div className={styles[`${pre}logo-wrapper`]}><img src={row.original.featuredImage} height={50} /></div>
        },
        {
            Header: 'Title',
            accessor: 'title'
        },
        {
            Header: 'Description',
            accessor: 'description'
        },
        {
            Header: 'Date',
            id: 'startDate',
            Cell: ({ row }) => <div className={styles[`${pre}date-wrapper`]}><div className="flex-row"><DateRangeIcon /><div className="flex-column">
                <span>{moment(row.original.startDate).format("DD/MM/YYYY HH:mm")}</span>
                <span>{moment(row.original.endDate).format("DD/MM/YYYY HH:mm")}</span>
            </div></div>
                <div className="flex-row"><AvTimerIcon /><span>{`${Math.ceil(row.original.duration / 60000)}min`}</span></div>
            </div>
        },
        {
            Header: "Category",
            accessor: "category.name",
            Cell: ({ row }) => <div className="category-chip">
                <span>{row.original.category.name}</span>
            </div>
        },
        {
            Header: "Promoter",
            accessor: "promoter.name",
            Cell: ({ row }) => <Link underline="hover" href={row.original.promoter.website}>{row.original.promoter.name}</Link>
        },
        {
            Header: 'Public',
            id: "isPublic",
            Cell: ({ row }) => row.original.isPublic ? <PublicIcon /> : <LockIcon />
        },
        {
            Header: 'Offline',
            id: "isOffline",
            Cell: ({ row }) => row.original.isOffline ? <GroupsIcon /> : <WifiIcon />
        },
        {
            Header: 'Actions',
            id: 'actions',
            Cell: ({ row }) => <TableActions id={row.original._id} updateDialog={updateDialog} deleteDialog={deleteDialog} url={row.original.url} />
        },
    ], []);

    const init = async () => {
        await readEvents.refetch();
    }
    React.useEffect(() => {
        init();
    }, [])

    React.useEffect(() => {
        if (readEvents.data) {
            const docs = get(readEvents, "data.data", [])
            const totalDocs = 0;
            documents.handleDocs(docs, totalDocs);
        }
    }, [readEvents.data]);

    return (
        <>
            <CssBaseline />
            {logged && <Toolbar objects={[
                {
                    name: "Aggiungi evento",
                    onClick: () => createDialog.handleOpen()
                }
            ]} />}
            <Table columns={columns} data={documents.docs} />
            {/* <div className={styles[`${pre}container`]}>
                {documents.docs && documents.docs.length && documents.docs.map((d, i) => <EventItem key={`${d.title}-${i}`} event={d} />)}
            </div> */}
            <CreateEventDialog
                title="Create event"
                dialog={createDialog}
                onSubmit={onCreateEvent}
            />
            <UpdateEventDialog
                title="Update event"
                dialog={updateDialog}
                onSubmit={onUpdateEvent}
            />
            <DeleteEventDialog
                title="Delete event"
                dialog={deleteDialog}
                onSubmit={onDeleteEvent}
            />
        </>
    )
}

export default withEventService(DashboardPage);